import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/layout'
import logger from '@/utils/logger'

Vue.use(VueRouter)

// 重写路由的push和replace方法，添加错误处理
const originalPush = VueRouter.prototype.push
const originalReplace = VueRouter.prototype.replace

VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => {
    if (err.name !== 'NavigationDuplicated' && 
        err.name !== 'NavigationCancelled') {
      logger.error('路由push错误:', err)
      // throw err
    }
    return Promise.resolve(false)
  })
}

VueRouter.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => {
    if (err.name !== 'NavigationDuplicated' && 
        err.name !== 'NavigationCancelled') {
      logger.error('路由replace错误:', err)
      // throw err
    }
    return Promise.resolve(false)
  })
}

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/login'),
    meta: { title: '登录' }
  },
  {
    path: '/finger',
    name: 'Finger',
    component: () => import('@/views/finger'),
    meta: { title: '浏览器指纹' }
  },
  {
    path: '/',
    component: Layout,
    redirect: '/course',
    children: [
      {
        path: 'course',
        name: 'Course',
        component: () => import('@/views/course/index.vue'),
        meta: { title: '课程列表', requireAuth: true }
      },
      {
        path: 'exam',
        name: 'Exam',
        component: () => import('@/views/exam/index.vue'),
        meta: { title: '考试中心', requireAuth: true }
      },
      {
        path: 'my',
        name: 'My',
        component: () => import('@/views/my/index.vue'),
        meta: { title: '个人中心', requireAuth: true }
      },
      {
        path: 'course/detail/:id',
        name: 'CourseDetail',
        component: () => import('@/views/course/detail.vue'),
        meta: { title: '课程详情', requireAuth: true }
      }
    ]
  },
  {
    path: '/practice',
    component: Layout,
    children: [
      {
        path: '',
        name: 'Practice',
        component: () => import('@/views/practice/index'),
        meta: { title: '刷题训练', requireAuth: true }
      },
      {
        path: 'exam/:id',
        name: 'PracticeExam',
        component: () => import('@/views/practice/exam'),
        meta: { title: '练习模式', requireAuth: true }
      }
    ]
  },
  {
    path: '/practice/detail/:id',
    name: 'PracticeDetail',
    component: () => import('@/views/practice/detail.vue'),
    meta: {
      title: '题库详情'
    }
  },
  {
    path: '/exam',
    component: Layout,
    children: [
      {
        path: '',
        name: 'Exam',
        component: () => import('@/views/exam/index.vue'),
        meta: { title: '考试中心', requireAuth: true }
      },
      {
        path: 'check/:id',
        name: 'ExamCheck',
        component: () => import('@/views/exam/check.vue'),
        meta: { title: '考试审核', requireAuth: true }
      },
      {
        path: 'paper/:id',
        name: 'ExamPaper',
        component: () => import('@/views/exam/paper.vue'),
        meta: { title: '考试详情', requireAuth: true }
      },
      {
        path: 'detail/:id',
        name: 'ExamDetail',
        component: () => import('@/views/exam/detail.vue'),
        meta: { title: '考试详情', requireAuth: true }
      },
      {
        path: 'thanks',
        name: 'ExamThanks',
        component: () => import('@/views/exam/thanks.vue'),
        meta: { title: '考试完成', requireAuth: true }
      }
    ]
  }
]

const router = new VueRouter({
  routes,
  scrollBehavior: () => ({ y: 0 })
})

// 白名单路由
const whiteList = ['/login', '/finger']

router.beforeEach(async (to, from, next) => {
  const token = localStorage.getItem('token')
  
  // 记录路由跳转信息
  logger.debug('路由跳转:', {
    from: from.fullPath,
    to: to.fullPath,
    hasToken: !!token
  })

  try {
    // 处理登录页面的特殊情况
    if (to.path === '/login') {
      if (token) {
        // 已登录时访问登录页，重定向到首页
        next('/')
      } else {
        // 未登录访问登录页，直接通过
        next()
      }
      return
    }

    // 处理其他页面
    if (token) {
      // 已登录状态
      next()
    } else {
      // 未登录状态
      if (whiteList.includes(to.path)) {
        next()
      } else {
        // 避免重复重定向
        if (from.path === '/login' && to.path === decodeURIComponent(from.query.redirect)) {
          next(false)
          return
        }

        // 保存原始目标路径，但要避免保存登录相关的路径
        const redirect = to.path === '/login' ? '/' : to.fullPath
        if (!redirect.includes('/login')) {
          next({
            path: '/login',
            query: { redirect }
          })
        } else {
          next('/login')
        }
      }
    }
  } catch (error) {
    logger.error('路由守卫错误:', error)
    next('/login')
  }
})

// 路由解析完成后的处理
router.afterEach((to) => {
  // 处理页面标题
  document.title = to.meta.title || '在线考试系统'
  
  // 清理可能存在的重复重定向
  if (to.path === '/login' && to.query.redirect) {
    const redirect = to.query.redirect
    if (redirect.includes('/login')) {
      router.replace('/login')
    }
  }
})

// 路由错误处理
router.onError((error) => {
  isNavigating = false
  
  // 处理不同类型的导航错误
  if (error.name === 'NavigationDuplicated') {
    logger.debug('路由重复:', error.message)
    return
  }
  
  if (error.name === 'NavigationCancelled') {
    logger.debug('路由取消:', error.message)
    return
  }
  
  if (error.name === 'NavigationAborted') {
    logger.debug('路由中止:', error.message)
    return
  }
  
  logger.error('路由错误:', error)
})

export default router 