import 'core-js/stable';
import 'regenerator-runtime/runtime';
import 'es6-promise/auto';
import 'whatwg-fetch';


import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import router from './router'

Vue.use(ElementUI)
Vue.config.productionTip = false

// 添加全局前置守卫
router.beforeEach((to, from, next) => {
  // 设置页面标题
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

// 忽略特定的 Chrome 扩展错误
window.addEventListener('error', (event) => {
  if (event.message.includes('The message port closed before a response was received') ||
      event.message.includes('Extension context invalidated')) {
    event.preventDefault()
    return false
  }
})

// 添加全局的未捕获 Promise 错误处理
window.addEventListener('unhandledrejection', (event) => {
  if (event.reason && event.reason.message && 
      (event.reason.message.includes('The message port closed') ||
       event.reason.message.includes('Extension context invalidated'))) {
    event.preventDefault()
    return false
  }
})

Vue.config.errorHandler = (err, vm, info) => {
  if (err.message && 
      (err.message.includes('The message port closed') ||
       err.message.includes('Extension context invalidated'))) {
    return
  }
  console.error('Vue Error:', err)
}

Vue.config.warnHandler = (msg, vm, trace) => {
  if (msg.includes('The message port closed') ||
      msg.includes('Extension context invalidated')) {
    return
  }
  console.warn('Vue Warning:', msg)
}

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
