<template>
  <div class="nav-header">
    <div class="nav-content">
      <div class="left">
        <div class="logo">在线考试系统</div>
        <el-menu mode="horizontal" :router="true" :default-active="activeMenu">
          <!-- <el-menu-item index="/course">课程学习</el-menu-item> -->
          <el-menu-item index="/exam">考试中心</el-menu-item>
          <!-- <el-menu-item index="/practice">刷题训练</el-menu-item>
          <el-menu-item index="/my">个人中心</el-menu-item> -->
        </el-menu>
      </div>
      <div class="user-info">
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link">
            <el-avatar 
              size="small" 
              :src="userInfo.avatar || defaultAvatar"
              class="user-avatar">
            </el-avatar>
            {{ userInfo.nickName || userInfo.userName || '用户' }}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <!-- <el-dropdown-item command="profile">个人信息</el-dropdown-item> -->
            <el-dropdown-item command="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
import { getInfo } from '@/api/login'
import defaultAvatar from '@/assets/default-avatar.png' // 需要添加默认头像图片

export default {
  name: 'NavHeader',
  data() {
    return {
      activeMenu: '/course',
      userInfo: {},
      defaultAvatar
    }
  },
  methods: {
    // 获取用户信息
    async getUserInfo() {
      try {
        const res = await getInfo()
        if (res.code === 200) {
          this.userInfo = res.data.user
          // 更新本地存储的用户信息
          localStorage.setItem('userInfo', JSON.stringify(res.data.user))
        }
      } catch (error) {
        console.error('获取用户信息失败:', error)
      }
    },
    // 处理下拉菜单命令
    handleCommand(command) {
      switch (command) {
        case 'profile':
          // 避免重复导航到当前页面
          if (this.$route.path !== '/my') {
            this.$router.push('/my').catch(() => {})
          }
          break
        case 'logout':
          this.logout()
          break
      }
    },
    // 退出登录
    logout() {
      localStorage.removeItem('token')
      localStorage.removeItem('userInfo')
      this.$router.push('/login')
      this.$message.success('已退出登录')
    }
  },
  watch: {
    $route: {
      handler(route) {
        this.activeMenu = route.path
      },
      immediate: true
    }
  },
  created() {
    // 组件创建时获取用户信息
    this.getUserInfo()
    
    // 尝试从本地存储获取用户信息
    const storedUserInfo = localStorage.getItem('userInfo')
    if (storedUserInfo) {
      this.userInfo = JSON.parse(storedUserInfo)
    }
  }
}
</script>

<style scoped>
.nav-header {
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(0,21,41,.08);
}

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
}

.left {
  display: flex;
  align-items: center;
}

.logo {
  font-size: 20px;
  font-weight: bold;
  margin-right: 40px;
  color: #409EFF;
}

.el-menu {
  border: none;
}

.el-menu--horizontal > .el-menu-item {
  height: 60px;
  line-height: 60px;
}

.user-info {
  display: flex;
  align-items: center;
}

.el-dropdown-link {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #606266;
}

.user-avatar {
  margin-right: 8px;
}

/* 修复下拉菜单箭头对齐 */
.el-icon--right {
  margin-left: 4px;
  margin-top: 1px;
}
</style> 