/**
 * 日志工具类
 * 
 * 使用方法:
 * 1. 引入logger
 * import logger from '@/utils/logger'
 * 
 * 2. 打印日志
 * logger.log('普通日志') 
 * logger.info('信息日志')
 * logger.warn('警告日志')
 * logger.error('错误日志') // 错误日志始终会打印
 * logger.debug('调试日志')
 * 
 * 3. 开启/关闭调试模式
 * logger.setDebug(true) // 开启调试模式
 * logger.setDebug(false) // 关闭调试模式
 * 
 * 注意:
 * - 在开发环境或开启调试模式时才会打印非error日志
 * - 可以通过 localStorage.debug = true 手动开启调试模式
 * - 调用 setDebug 后会自动刷新页面
 */

const isDebug = true

const logger = {
  log(...args) {
    if (isDebug) {
      console.log(...args)
    }
  },

  info(...args) {
    if (isDebug) {
      console.info(...args)
    }
  },

  warn(...args) {
    if (isDebug) {
      console.warn(...args)
    }
  },

  error(...args) {
    // 错误日志始终打印
    console.error(...args)
  },

  debug(...args) {
    if (isDebug) {
      console.debug(...args)
    }
  },

  // 开启/关闭调试模式
  setDebug(enabled) {
    if (enabled) {
      localStorage.setItem('debug', 'true')
    } else {
      localStorage.removeItem('debug')
    }
    window.location.reload()
  }
}

export default logger